import React from 'react';

import './styles/slider.css';
import $ from 'jquery';
class Basic extends React.Component {

    constructor() {
        super();
        this.state = {
            started: true,
            imageUrl01: '../../../../public/imageContent/slide1.jpg'
        }
    }

    /* eslint-disable */
    _init() {

        var interval_id = 0;
        require.ensure([], (require) => {
            //require('../../lib/jquery.flexslider');
            //var $ = require('jquery') + require('../../lib/jquery.flexslider');
            var $ = require('jquery')
            var shortcut = require('../../utils/shortcut');

            var application = require('../../utils/application');

            $(document).ready(function() {
                var length = 0,
                    currentIndex = 0,
                    hasStarted = false, //
                    t = 3000; //
                if(ENV.imagesConfig.time != 0 && ENV.imagesConfig.time != null)
                {
                    t = ENV.imagesConfig.time;
                }

                length = $('.slider-panel').length;
                //
                $('.slider-panel:not(:first)').hide();
                //slider-item
                $('.slider-item:first').addClass('slider-item-selected');
                //
                $('.slider-page').hide();
                //,
                $('.slider-panel, .slider-pre, .slider-next').hover(function() {
                    stop();
                    $('.slider-page').show();
                }, function() {
                    $('.slider-page').hide();
                    start();
                });
                $('.slider-item').hover(function(e) {
                    stop();
                    // filter() 
                    var preIndex = $(".slider-item").filter(".slider-item-selected").index();
                    currentIndex = $(this).index();
                    play(preIndex, currentIndex);
                }, function() {
                    start();
                });
                $('.slider-pre').unbind('click');
                $('.slider-pre').bind('click', function() {
                    pre();
                });
                $('.slider-next').unbind('click');
                $('.slider-next').bind('click', function() {
                    next();
                });
                /**
                 * 
                 */
                function pre() {
                    var preIndex = currentIndex;
                    currentIndex = (--currentIndex + length) % length;
                    play(preIndex, currentIndex);
                }
                /**
                 * 
                 */
                function next() {
                    var preIndex = currentIndex;
                    currentIndex = ++currentIndex % length;
                    play(preIndex, currentIndex);
                }
                /**
                 * preIndexcurrentIndex
                 * preIndex 
                 * currentIndex 
                 */
                function play(preIndex, currentIndex) {
                    // fadeIn() 
                    $('.slider-panel').eq(preIndex).fadeOut(500)
                        .parent().children().eq(currentIndex).fadeIn(1000);
                    $('.slider-item').removeClass('slider-item-selected');
                    $('.slider-item').eq(currentIndex).addClass('slider-item-selected');
                }
                /**
                 * 
                 */
                function start() {
                    if(!hasStarted) {
                        hasStarted = true;
                        interval_id = setInterval(next, t);
                    }
                }
                /**
                 * 
                 */
                function stop() {
                    clearInterval(interval_id);
                    hasStarted = false;
                }
                //
                start();

                var wHistory = window.history;

                /*
                console.log('O');
                window.location.href = 'http://127.0.0.1:6600/#/first'
                */

                /**
                 * 
                 * @param keyDefault
                 */
                function shortCutDefault(keyDefault){
                    var valueDefault= shortcut.keyboardDefault[keyDefault];
                    if(ENV.keyboard[keyDefault] != null || ENV.keyboard[keyDefault] != ''){
                        valueDefault= ENV.keyboard[keyDefault]
                    }
                    return valueDefault;
                }

                // 
                var imagePlayOn = shortCutDefault('imagePlayOn');
                shortcut.add(
                    imagePlayOn,
                    function () {
                        console.log('');
                        localStorage.imageTimer='on';
                        interval_id = setInterval(next, t);
                        console.log(interval_id);
                        localStorage.carousel_interval_id= interval_id;
                        application.autoFocus('app');
                        // 
                        //window.history.go(0);

                        //var int_play = setInterval(play, ENV.router.inteval);
                    },
                    {
                        'type': 'keydown',
                        'propagate': true,
                        'target': document
                    });

                // 
                var imagePlayOff = shortCutDefault('imagePlayOff');
                shortcut.add(
                    imagePlayOff,
                    function () {
                        console.log('');
                        localStorage.imageTimer='off';

                        console.log(interval_id)
                        clearInterval(interval_id);
                        application.autoFocus('app');
                        // 
                        //window.history.go(0);
                        //timer_flag = cookie.getCookie('timer');
                    },
                    {
                        'type': 'keydown',
                        'propagate': true,
                        'target': document
                    });


            });



        },'CarouselScreenJqueryShortUp')

    }



    _initKeyDown() {
        require.ensure([], (require) => {

            var interval_id = 0;
            //require('../../lib/jquery.flexslider');
            //var $ = require('jquery') + require('../../lib/jquery.flexslider');

            var $ = require('jquery')
            var shortcut = require('../../utils/shortcut');

            var application = require('../../utils/application');

            $(document).ready(function() {
                var length = 0,
                    currentIndex = 0,
                    hasStarted = false, //
                    t = 3000; //
                if(ENV.imagesConfig.time != 0 && ENV.imagesConfig.time != null)
                {
                    t = ENV.imagesConfig.time;
                }

                length = $('.slider-panel').length;
                //
                $('.slider-panel:not(:first)').hide();
                //slider-item
                $('.slider-item:first').addClass('slider-item-selected');
                //
                $('.slider-page').hide();
                //,
                $('.slider-panel, .slider-pre, .slider-next').hover(function() {
                    stop();
                    $('.slider-page').show();
                }, function() {
                    $('.slider-page').hide();
                    start();
                });
                $('.slider-item').hover(function(e) {
                    stop();
                    // filter() 
                    var preIndex = $(".slider-item").filter(".slider-item-selected").index();
                    currentIndex = $(this).index();
                    play(preIndex, currentIndex);
                }, function() {
                    start();
                });
                $('.slider-pre').unbind('click');
                $('.slider-pre').bind('click', function() {
                    pre();
                });
                $('.slider-next').unbind('click');
                $('.slider-next').bind('click', function() {
                    next();
                });
                /**
                 * 
                 */
                function pre() {
                    var preIndex = currentIndex;
                    currentIndex = (--currentIndex + length) % length;
                    play(preIndex, currentIndex);
                }
                /**
                 * 
                 */
                function next() {
                    var preIndex = currentIndex;
                    currentIndex = ++currentIndex % length;
                    play(preIndex, currentIndex);
                }
                /**
                 * preIndexcurrentIndex
                 * preIndex 
                 * currentIndex 
                 */
                function play(preIndex, currentIndex) {
                    // fadeIn() 
                    $('.slider-panel').eq(preIndex).fadeOut(500)
                        .parent().children().eq(currentIndex).fadeIn(1000);
                    $('.slider-item').removeClass('slider-item-selected');
                    $('.slider-item').eq(currentIndex).addClass('slider-item-selected');
                }
                /**
                 * 
                 */
                function start() {
                    if(!hasStarted) {
                        hasStarted = true;
                        interval_id = setInterval(next, t);
                    }
                }
                /**
                 * 
                 */
                function stop() {
                    clearInterval(interval_id);
                    hasStarted = false;
                }


                function shortCutDefault(keyDefault) {
                    var valueDefault = shortcut.keyboardDefault[keyDefault];
                    if (ENV.keyboard[keyDefault] != null || ENV.keyboard[keyDefault] != '') {
                        valueDefault = ENV.keyboard[keyDefault]
                    }
                    return valueDefault;
                }

                // 
                var imagePlayOn = shortCutDefault('imagePlayOn');
                shortcut.add(
                    imagePlayOn,
                    function () {
                        console.log('');
                        localStorage.imageTimer = 'on';
                        application.autoFocus('app');
                        // 
                        //window.history.go(0);
                        //
                        start();
                        //var int_play = setInterval(play, ENV.router.inteval);
                    },
                    {
                        'type': 'keydown',
                        'propagate': true,
                        'target': document
                    });

                // 
                var imagePlayOff = shortCutDefault('imagePlayOff');
                shortcut.add(
                    imagePlayOff,
                    function () {
                        console.log('');
                        localStorage.imageTimer='off';

                        console.log(interval_id)
                        clearInterval(interval_id);
                        application.autoFocus('app');
                        // 
                        //window.history.go(0);
                        //timer_flag = cookie.getCookie('timer');
                    },
                    {
                        'type': 'keydown',
                        'propagate': true,
                        'target': document
                    });
            });
        },'CarouselScreenShortUp')

    }

    componentDidMount() {

        console.log(ENV.imagesConfig);

        // 

        this._init()
        /*
        if(ENV.imagesConfig.imagePlay)
        {
            if(localStorage.imageTimer == 'on') {

            }
            else if(localStorage.imageTimer == 'off') {
                this._initKeyDown()
            }


        }
        */

        // document.getElementById('cs101').innerHTML = ''


        // div
        var img01 = document.createElement("img");


    }

    componentWillMount() {
        //console.log('ImagesConfig.image01.url')
        //console.log(ImagesConfig.image01.url)
    }


    render() {
        var divStyle = {
            position: 'absolute',
            top: 0,
            left: 0,
            width:1920,
            height: 1080
        }
        //console.log(ImagesConfig.image01.url)

        var image01 = '../../../../public/imagesConfig/images/slide1.jpg';

      


        return (
            <div id='cs101'>
                <div className={'slider'} style={divStyle}>
                    <ul className={"slider-main"}>
                        {ENV.imagesConfig.icon.map(function(i,v)
                            {
                                return (
                                    <li className={"slider-panel"} key={i}>

                                        {
                                            ENV.dev?
                                            <img src={'imageContent/'+ENV.imagesConfig.icon[v]} ></img>:
                                            <img src={'imageContent/'+ENV.imagesConfig.icon[v]} ></img>
                                        }


                                    </li>)
                            })
                        }


                    </ul>

                    <div className={'slider-extra'}>
                        <ul className={'slider-nav'}>
                            {ENV.imagesConfig.icon.map(function(i,v){
                                console.log('i');
                                console.log(i);
                                console.log('v');
                                console.log(v);
                                return (
                                    <li className={"slider-item"} key={i}>
                                        {v+1}
                                    </li>)
                            })}


                        </ul>
                        <div >
                            <a> </a>
                            <a> </a>
                        </div>
                    </div>

                </div>
            </div>
        )
    }
    /* eslint-enable */
}

export default Basic;



